@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Gloria+Hallelujah&family=Gochi+Hand&family=Permanent+Marker&display=swap);
.App {
  text-align: center;
  font-family: 'Bebas Neue', cursive;
}
.header {
    text-align: center;
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
    font-size: 150%;
    color: lightslategray;
}
.eighteenIcon {
    height: 100px;
    border-radius: 50%;
    margin-bottom: 100px;
}
.navbar {
    display:flex;
    justify-content: space-around;
    align-items: center;
    background-color: lightslategray;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 1;
}
.navIcon {
    height: 60px;
    background-color: white;
    border-radius: 50%;
    margin: 20%;
}
.searchContainer {
    position: relative;
}
input[type="search"] {
    
	display: block; 
    width: 100%; 
    font-family: 'Architects Daughter', cursive;
	letter-spacing: 1px;        
	text-shadow: 0 0 2px black;       
    font-size: large;
    border: 1px solid grey;
    border-radius: 100px;
    padding: 2px 23px 2px 30px;
    outline: 0;
    height: 25px;
}
input[type="submit"] {  
	display: block; 
    width: 50%; 
    font-family: 'Architects Daughter', cursive;
    letter-spacing: 1px;     
	text-shadow: 0 0 2px black;       
    border: 1px solid aliceblue;
    border-radius: 100px;
    height: 25px;
    cursor: pointer;
}
input[type="submit"]:hover {  
    color: black;   
    border: 1px solid black;
    font-weight: 900;
	text-shadow: 0 0 2px white;       
}
.search-icon {
    position: absolute;
    top: 6px;
    left: 8px;
    width: 14px;
}
.options {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    color: white;
    text-shadow: 0 0 0.5px black;
    width: 100%;
}
.optionPost {
    background-color: lightslategray;
    margin: 5px auto;
    cursor: pointer;
    width: 100%;
    display: flex;
    align-items: center;
    border-radius: 100px;
}
.optionName {
    width: 90%;
}
.optionIconDiv {
    width: 10%;
    margin: 5px;
}
.optionIcon {
    width: 100%;
    border-radius:50%;
}
.about {
    margin: auto;
    font-size: 120%;
    max-width: 60%;
}
.darkWebImg {
    max-height: 400px;
    border-radius: 20%;
    transition: 1s;
    cursor: pointer;
    margin: 2%;
}
.darkWebImg:hover {
    transition: 1s;
    border-radius: 5%;
}
.aboutH3 {
    font-size: 150%;
    text-decoration: underline;
    color: rgb(193, 171, 6)
}
footer {
    background: lightslategray;
    bottom: 0;
    width: 100%;
    margin: 50px 0;
    padding: 10px;
    color: white;
}
.ul {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    top: 0;
    z-index: 1;
    list-style-type: none;
}
.Icon {
    height: 50px;
    width: 50px;
    transition: 1500ms;
}
.Icon:hover {
    border-radius: 50%;
    border: 1px solid aliceblue;
    background-color: aliceblue;
    transition: 1500ms;
    height: 150%;
}
.circle {
    border-radius: 50%;
} 
.name {
    color: aliceblue;
    -webkit-text-stroke: 0.2px black;
    transition: 1500ms;
}
.name:hover {
    color: black;
    transition: 1500ms;
}
.four0four {
    text-align: center;
}
.home {
    margin: 20px auto;
    width: 150px;
    color: aliceblue;
}
.home Img {
    max-height: 100px;
    border-radius: 50%;
    margin: 10px;
    transition: 3000ms;
}
.home Img:hover {
    background-color: aliceblue;
    transition: 3000ms;
}

.firstLine {
    display: flex;
    justify-content: space-around;
}
.postsH2 {
    font-size: 250%;
}
.post {
    margin: 40px auto;
    max-width: 800px;
    padding:10px;
    box-shadow: 0px 0px 10px;
    position: relative;
    background-color: white;
}
.hiddenPost {
    display: none;
}
.hideTicketButton{
    position: absolute;
    right: 0;
    background: none;
    border: none;
    text-decoration: underline;
    color: rgb(170,170,170);
    cursor: pointer;
}
h2 {
    padding:0;
    color: lightslategray;
    text-align: center;
}
.post {
    margin: 40px auto;
    max-width: 800px;
    padding:10px;
    box-shadow: 0px 0px 10px;
    position: relative;
    background-color: white;
}
.footer {
    margin: 10px 0; 
    padding: 3px;
    background-color: black;
}
.info {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    color: lightslategray;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.info div {
    margin: auto 1%;
}
.content {
    overflow-wrap: break-word;
}
.author {
    width: 33%;
    text-align: left;
}
.tags {
    width: 33%;
    color: aliceblue;
}
.tag {
    margin: auto 5%;
}
.date {
    width: 33%;
    text-align: right;
}
.counter{
    margin:auto;
    max-width: 800px;
    background-color: lightslategray;
    padding:0px 10px;
    border-radius: 5px;
    font-size: 180%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.counterSentence {
    color: rgb(49, 47, 43);
    font-weight: bold;
    width: 33%;
}
#restoreHidePosts{
    background:none;
    border: none;
    text-decoration: underline;
    color: white;
    cursor: pointer;
    font-size: 70%;
}

.postId {
    margin: 40px auto;
    max-width: 800px;
    padding:10px;
    box-shadow: 0px 0px 10px;
    position: relative;
    background-color: white;
    font-size: 140%;
}
